@use 'base/variable' as v;

.alert {
    padding: 0.5rem !important; 
    color: v.$white;
    width: 100%;
    display: block;
    margin: 1rem 0;
    text-align: center;
    font-weight: v.$bold;
    text-transform: uppercase;
    &.error {
        background-color: v.$red;
    }
    &.success {
        background-color: v.$green;
    }
}

