  height: 100%;
  overflow-y: scroll;
}

body {
  min-height: 100%;
  max-width: 100%;
  display: flex;
  flex-direction: column;
  background-color: #FFFFFF;
  color: #333333;
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}
