/* Estilos para hacer la tabla responsive en dispositivos pequeños */
@media only screen and (max-width: 768px) {
  th, td {
    display: block;
    width: 100%;
    box-sizing: border-box;
  }
  /* Ocultar encabezado, excepto el encabezado Acciones */
  th:not(:last-child) {
    display: none;
  }
  tr {
    margin-bottom: 1rem; /* Espacio entre filas */
  }
  td:before {
    content: attr(data-label); /* Usar el atributo data-label como contenido antes del texto de la celda */
    font-weight: bold;
    display: block;
    margin-bottom: 0.5rem; /* Espacio entre el encabezado y el contenido de la celda */
  }
}
