@use 'variable' as v;
@use 'base/mixins' as m;
#dni {
    width: 100% !important;
    @include m.tablet {
        width: 100% !important;
    }
}
.slot {
    width: 100% !important;
    @include m.tablet {
        width: auto !important;
    }
}
.form-admin {
    width: 100% !important;
    @include m.tablet {
        width: auto !important;
    }
    padding: 1rem;
    .button {
        width: 100%;
    };
}
.index-admin-container {
    width: 100% !important;
    @include m.tablet {
        width: auto !important;
    }
}

table {
    width: 100%;
    border-collapse: collapse;
    margin-bottom: 1rem; /* Espacio entre la tabla y otros elementos */
    //margin: 1rem !important;
    a {
        //background-color: beige;
        color: black !important;
        margin: 0 2rem 0 0 !important;
    }
    .button {
    }
}

th, td {
    border: 1px solid #ddd;
    padding: 8px;
    text-align: left;
    
}


/* Estilos para hacer la tabla responsive en dispositivos pequeños */
@media only screen and (max-width: 768px) {
    th, td {
        display: block;
        width: 100%;
        box-sizing: border-box;
    }

    /* Ocultar encabezado, excepto el encabezado Acciones */
    th:not(:last-child) {
        display: none;
    }

    tr {
        margin-bottom: 1rem; /* Espacio entre filas */
    }

    td:before {
        content: attr(data-label); /* Usar el atributo data-label como contenido antes del texto de la celda */
        font-weight: bold;
        display: block;
        margin-bottom: 0.5rem; /* Espacio entre el encabezado y el contenido de la celda */
    }
}
.tableInfoField {
    display: block !important;
    @include m.tablet {
        display: none !important
    }
}
.form-admin-edit {
    width: 100%;
    .button {
        width: 100% !important;
    }
    @include m.tablet {
        width: 50%;
    }
}
.form-admin-edit-container {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    
}

#color,#rgb {
    color: black !important;
}
.form-admin {
    width: 100% !important;
    @include m.tablet {
        width: 50% !important;
    }
}